
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'

import { getGoodsList } from '../../service/getData'

export default {

data(){
return{
title: '',
id:'',
n: 1,
isempty:false,
status: {
pullupStatus: 'default',
pulldownStatus: 'default'
},
list: []
}
},
components: {
XHeader,
Scroller,
LoadMore,
XImg
},
mounted(){
this.title = this.$route.query.name
this.id = this.$route.query.id

getGoodsList(1, this.id).then(resolved => {
console.log(resolved.data)
this.$vux.loading.hide()
if(resolved.data.length!=0){
this.list = resolved.data
this.isempty = false
}else{
this.isempty = true
}
},this)
},
methods: {
success(src, ele) {
const span = ele.parentNode.querySelector('span')
ele.parentNode.removeChild(span)
},
error(src, ele, msg) {
console.log('error load', msg, src)
const span = ele.parentNode.querySelector('span')
span.innerText = '加载失败'
},
openDetail(index) {
console.log(index)
this.$router.push({
name: 'Commodity',
query: {
id: index
}
});
},
loadMore () {//加载更多
setTimeout(() => {
this.n += 1
this.$nextTick(() => {
this.$refs.scroller.donePullup()

getGoodsList(this.n, this.id).then(resolved => {
console.log(resolved.data)
if(resolved.data.length==0){
this.$refs.scroller.disablePullup()
}else{
this.list = resolved.data
this.$vux.loading.hide()
console.log(this.list[0].attachmentCover)
}
})
})
}, 2000)
},
refresh () {//数据刷新
setTimeout(() => {
this.n = 1
this.$nextTick(() => {

getGoodsList(this.n, this.id).then(resolved => {
this.list = resolved.data
this.$vux.loading.hide()
console.log(this.list[0].attachmentCover)
})
setTimeout(() => {
this.$refs.scroller.donePulldown()
this.pullupEnabled && this.$refs.scroller.enablePullup()
}, 10)
})
}, 2000)
},
changePullupStatus (enabled) {//是否禁用上拉加载更多
if (enabled) {
this.$refs.scroller.enablePullup()
this.pullupEnabled = true
} else {
this.$refs.scroller.disablePullup()
this.pullupEnabled = false
}
}
}
}



